import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "首页",
    component: () => import("@/views/HomeView"),
    meta: {
      show: true,
      iconClass: "home",
      title: "首页",
    },
  },
  {
    path: "/all",
    name: "全家桶",
    redirect: "/all/websourcedownload",
    meta: {
      show: true,
      iconClass: "toolbox",
    },
    children: [
      {
        path: "/all/websourcedownload",
        name: "网页活动图片资源下载工具",
        component: () => import("@/views/all/WebsouceDownload"),
        meta: {
          show: true,
          iconClass: "images",
          title: "网页活动图片资源下载工具",
        },
      },
    ],
  },
  {
    path: "/:path(.*)*",
    name: "notFound",
    component: () => import("@/views/errors/404"),
    meta: {
      show: false,
      iconClass: "toolbox",
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
